import { render } from '@testing-library/preact';
import { expect } from 'chai';
import { Chip } from '../Chip';
import userEvent from '@testing-library/user-event';
import { TabbableModeContext } from '../../hooks/UNSAFE_useTabbableMode';

describe('Test Chip component', () => {
  it('render', async function () {
    const content = render(<Chip>11</Chip>);
    const text = await content.findByText('11');
    expect(text).not.null;
  });

  it('isSelected property', async function () {
    const content = render(<Chip isSelected={true}>88</Chip>);
    const firstElement = content.container.firstElementChild;
    // @ts-ignore
    const classList = firstElement.classList;
    expect(classList.length).equals(4);
  });

  it('isDisabled property', async function () {
    const content = render(<Chip isDisabled={true}>99</Chip>);
    const firstElement = content.container.firstElementChild;
    // @ts-ignore
    const classList = firstElement.classList;
    expect(classList.length).equals(3);
  });

  it('event', async function () {
    let count = 0;
    const increment = () => count++;
    const content = render(<Chip onToggle={increment}>Press</Chip>);
    const label = await content.findByText('Press');
    expect(label).not.null;
    userEvent.click(label);
    expect(count).equals(1);
  });

  it('with TabbableModeContext isTabbable=true', async () => {
    const content = render(
      <TabbableModeContext.Provider value={{ isTabbable: true }}>
        <Chip>Test</Chip>
      </TabbableModeContext.Provider>
    );
    const chip = await content.findByText('Test');
    const tabindex = chip.getAttribute('tabindex');
    expect(tabindex).to.equal('0');
  });

  it('with TabbableModeContext isTabbable=false', async () => {
    const content = render(
      <TabbableModeContext.Provider value={{ isTabbable: false }}>
        <Chip>Test</Chip>
      </TabbableModeContext.Provider>
    );
    const chip = await content.findByText('Test');
    const tabindex = chip.getAttribute('tabindex');
    expect(tabindex).to.equal('-1');
  });
});
